.contacts {
  &__block {
    @include pageGrid;
    row-gap: 48px;

    &--form {
      grid-column: 1 / -1;

      @include onTablet {
        grid-column: 1 / 4;
      }

      @include onDesktop {
        grid-column: 1 / 6;
      }
    }

    &--info {
      grid-column: 1 / -1;

      @include onTablet {
        grid-column: 4 / 6;
      }

      @include onDesktop {
        grid-column: 8 / 11;
      }
    }
  }
}
